<template>
  <div class="firmList bg-light">
    <loading :active.sync="isLoading" loader="bars"></loading>
    <div class="banner position-relative">
      <div class="bannerText position-absolute text-dark display-4 font-weight-bold">尋找寄宿</div>
      <img
        class="img-fluid"
        src="https://upload.cc/i1/2020/09/02/JE5b96.png"
        style="height: 200px;object-fit: cover; width: 100%;"
        alt
      />
    </div>

    <div class="container py-3">
      <ul class="nav nav-tabs" id="myTab" role="tablist">
        <li class="nav-item w-50" role="presentation">
          <a
            class="nav-link active"
            id="firmLst-tab"
            data-toggle="tab"
            href="#firmLst"
            role="tab"
            aria-controls="firmLst"
            aria-selected="true"
          >廠商列表</a>
        </li>
        <li class="nav-item w-50" role="presentation">
          <a
            class="nav-link"
            id="roomList-tab"
            data-toggle="tab"
            href="#roomList"
            role="tab"
            aria-controls="roomList"
            aria-selected="false"
          >房間列表</a>
        </li>
      </ul>
      <div class="tab-content" id="myTabContent">
        <div
          class="tab-pane fade show active"
          id="firmLst"
          role="tabpanel"
          aria-labelledby="firmLst-tab"
        >
          <firmListFirm :firmList="firmList" @reset="searchFirm" @search="searchFirm"></firmListFirm>
          <page v-if="firmList[0] != null" :page-data="pagelist" @page-change="getFirmData"></page>
        </div>
        <div class="tab-pane fade" id="roomList" role="tabpanel" aria-labelledby="roomList-tab">
          <firmListRoom></firmListRoom>
        </div>
      </div>
    </div>
    <div class="container py-5"></div>
  </div>
</template>

<script>
/* global $ */
import VueLoading from 'vue-loading-overlay'
import 'vue-loading-overlay/dist/vue-loading.css'
import page from '@/components/page.vue'
import firmListFirm from '@/components/firmListFirm.vue'
import firmListRoom from '@/components/firmListRoom.vue'

export default {
  data () {
    return {
      firmList: {},
      pagelist: {},
      isLoading: false,
      searchConfig: {
        keyword: '',
        evaluation: '',
        money: '',
        area: '',
        country: ''
      }
    }
  },
  created () {
    this.getFirmData()
    this.getRoomData()
    $('html, body').animate(
      {
        scrollTop: $('#app').offset().top
      },
      0
    )
  },
  components: { page, loading: VueLoading, firmListFirm, firmListRoom },
  methods: {
    getFirmData: function (page = 1) {
      this.isLoading = true
      const vm = this
      const config = {
        method: 'get',
        url: `http://pettrip.rocket-coding.com/api/Room/GetCompanys?page=${page}&paged=6&keyword=${this.searchConfig.keyword}&money=${this.searchConfig.money}&evaluation=${this.searchConfig.evaluation}&country=${this.searchConfig.country}&area=${this.searchConfig.area}`
      }
      this.$http(config)
        .then(function (response) {
          console.log(response)
          vm.firmList = response.data.companies
          vm.pagelist = response.data.meta
          vm.isLoading = false
        })
        .catch(function (error) {
          console.log(error)
          vm.isLoading = false
        })
    },
    searchFirm: function (data) {
      this.searchConfig = data
      this.getFirmData()
    },
    getRoomData: function () {
      const config = {
        method: 'get',
        url:
          'http://pettrip.rocket-coding.com/api/Room/GetRoom?chk_cat=false&chk_dog=true&dates=2020-09-23&datee=2020-09-24'
      }

      this.$http(config)
        .then(function (response) {
          console.log(response)
        })
        .catch(function (error) {
          console.log(error)
        })
    }
  }
}
</script>
